.Header {
  height: 65px;
  position: absolute;
  overflow: hidden;
  width: 100%;
  max-width: 1400px;
  margin: 0px auto;
}

.Nav {
  // display: flex;
  // align-items: center;
  // justify-content: space-between;
  // height: 70px;
  // position: relative;
}

.AllNavigation {
  position: absolute;
  display: flex;
  z-index: 1;
  top: 20px;
  right: 0;
}

.logo {
  font-size: 18px;
  color: floralwhite;
  font-weight: 1000;
  font-family: 'Mulish';
  margin: 23px;
  cursor: pointer;
  z-index: 1;
  position: absolute;
}

.Navigation {
  color: #d6d6d6;
  font-family: 'Nunito Sans', sans-serif;
  font-weight: 600;
  display: flex;
  font-size: 20px;
  transition: 0.4s;

  .NavOne,.NavTwo{
    transition: 0.4s;
  }

  .NavOne:hover{
    color: white;
  }
  .NavTwo:hover{
    color: white;
  }
}

.Contacts {
  align-items: center;
}

.Inst {
  width: 27px;
  cursor: pointer;
  margin-right: 20px;
}

.Tg {
  width: 26px;
  margin-right: 30px;
  cursor: pointer;
}

.WhtsApp {
  width: 27px;
  margin-right: 20px;
  cursor: pointer;
}

@media (max-width: 539px) {
  .AllNavigation {}
}